import React, { useContext } from 'react';
import './ProgresBar.css';
import StyleContext from './../../ContextStyle';

const ProgressBar = ({ completed }) => {
  const { isDark } = useContext(StyleContext);
  return (
    <div className={isDark ? 'dark-mode progressBar' : 'progressBar'}>
      <div
        style={{
          height: '100%',
          width: `${completed}%`,
          backgroundColor: '#536DFE',
          borderRadius: 'inherit',
          textAlign: 'right',
        }}>
        <span
          style={{
            padding: 5,
            color: 'black',
            fontWeight: 'bold',
          }}></span>
      </div>
    </div>
  );
};

export default ProgressBar;
