import React, { useContext } from 'react';
import Headroom from 'react-headroom';
import Toggle from '../Tooggle/Toggle';
import './Header.css';
import StyleContext from './../../ContextStyle';
const logoText = 'Chiranthan';

const Header = () => {
  const { isDark } = useContext(StyleContext);
  return (
    <Headroom>
      <div className={isDark ? 'dark-mode header' : 'header'}>
        <div className='header__left'>
          <a href='/my-web' className='header__logo'>
            {`< ${logoText} />`}
          </a>
        </div>
        <div className='header__right'>
          <ul className='header__items'>
            <li>
              <a href='#skills' className='header__list'>
                Skills
              </a>
            </li>
            <li>
              <a href='#proficiency' className='header__list'>
                Proficiency
              </a>
            </li>
            <li>
              <a href='#contact' className='header__list'>
                Contact me!
              </a>
            </li>
          </ul>
          <div className='header__toggle'>
            <Toggle />
          </div>
        </div>
      </div>
    </Headroom>
  );
};

export default Header;
